import { EmailRounded, InboxRounded } from '@mui/icons-material';
import { Paper, Typography } from '@mui/material';
import AlternateEmailIcon from '@mui/icons-material/AlternateEmail';
import { ShowUpAnimated } from 'components/animated/showUp';
import React from 'react';

export function MessagesView({ messages, goto }) {
  console.log(messages);
  return (
    <div className="row">
      <div className="col-12">
        <div className="row">
          <ShowUpAnimated bottom delay={0.5}>
            <div className="col-12 p-3 text-center">
              <InboxRounded className="mx-3" fontSize="large" />
              <Typography align="center" variant="h5">
                پیام های من
              </Typography>
            </div>
          </ShowUpAnimated>
        </div>
        {messages.map((item, index) => (
          <div className="row" key={`msg-${item._id}-${index}`}>
            <div className="col-12 p-3">
              <ShowUpAnimated bottom delay={index / 4}>
                <Paper onClick={() => goto(item._id)} sx={{ cursor: 'pointer' }}>
                  <div className="row">
                    <div className="col-sm-6 p-3">
                      <Typography>
                        <EmailRounded className="mx-3" />
                        {item.Title}
                      </Typography>
                    </div>
                    <div className="col-sm-6 p-3" dir="ltr">
                      <Typography variant="body2" color="GrayText">
                        <AlternateEmailIcon fontSize="small" className="mx-3" />
                        {item.Creator.Username}
                      </Typography>
                    </div>
                  </div>
                </Paper>
              </ShowUpAnimated>
            </div>
          </div>
        ))}
      </div>
    </div>
  );
}

export default MessagesView;
